import { css } from 'lit';

export const styles = css`:host {
  --elevation: var(--ig-snackbar-elevation, var(--ig-elevation-4));
  --background: var(--ig-snackbar-background, hsla(var(--ig-gray-900), 0.9));
  --text-color: var(--ig-snackbar-text-color, var(--ig-gray-900-contrast));
  --button-color: var(--ig-snackbar-button-color, hsla(var(--ig-secondary-500), var(--ig-secondary-a)));
  --border-radius: var(--ig-snackbar-border-radius, clamp(0rem, calc(var(--ig-radius-factor, 0.0833333333) * 1.5rem), 1.5rem));
}

[part~=base] {
  border-radius: var(--border-radius);
  color: var(--text-color);
  background: var(--background);
  -webkit-box-shadow: var(--elevation);
          box-shadow: var(--elevation);
}

[part~=message] {
  font-family: var(--ig-caption-font-family, var(--ig-font-family));
  font-size: var(--ig-caption-font-size);
  font-weight: var(--ig-caption-font-weight);
  font-style: var(--ig-caption-font-style);
  line-height: var(--ig-caption-line-height);
  letter-spacing: var(--ig-caption-letter-spacing);
  text-transform: var(--ig-caption-text-transform);
  margin-top: var(--ig-caption-margin-top);
  margin-bottom: var(--ig-caption-margin-bottom);
}

igc-button::part(base),
igc-button::part(base):hover {
  color: var(--button-color);
}

igc-button::part(base):focus-visible::after {
  display: none;
}`;
